import config from '../config'
import axios from 'axios'


async function Post (urls , callback) {
  axios({
    method: 'post',
    url: config.Host+'api',
    headers: {},
    data: {
      longURL: urls,
    }
  }).then(response =>{
    const code = response.data
    callback(null,code);
  });

}


export default Post;
